@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
*,::before,::after{margin:0;padding:0;box-sizing:border-box}#root{padding:0 10px}body{background-color:#16191c;color:white;font-family:'Poppins';font-weight:400}a,a:visited{color:white;text-decoration:none;transition:color linear 0.3s}a:hover{color:#20c997}ul{list-style:none}.navigation{display:flex;align-items:center;padding:20px 0;margin-bottom:50px}.navigation img{width:50px;margin-right:20px;display:block}.navigation a{position:relative;padding:15px 10px;margin:0 5px}.navigation a.nav-active::after{content:'';position:absolute;bottom:0;left:0;width:100%;height:5px;background-color:#20c997}.navigation a.nav-active{color:#20c997}.countries .sort-container{width:100%;padding:0 10px;margin:0 0 50px;color:#20c997;display:flex;align-items:center}.countries .sort-container label{font-size:1.1rem}.countries .sort-container ul{display:flex;flex-wrap:wrap}.countries .sort-container ul li{margin:0 10px;display:flex;align-items:center}.countries .sort-container ul li input{width:30px}.countries .sort-container ul li label{font-size:1.1rem;cursor:pointer}.countries .sort-container .cancel{margin-left:30px;background-color:#20c997;cursor:pointer;border-radius:10px}.countries .sort-container .cancel h5{padding:0 10px;color:#16191c;font-size:1.1rem}.card{position:relative;display:inline-block;width:250px;height:170px;margin:20px;border-radius:50px;overflow:hidden;cursor:pointer;transition:border-radius linear 0.3s}.card::before{content:'';position:absolute;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,0.7);z-index:1;opacity:0;transition:opacity linear 0.3s}.card:hover{border-radius:0%}.card:hover::before{display:block;opacity:1}.card:hover .data-container{display:flex;opacity:1}.card:hover img{-webkit-transform:scale(1, 1);transform:scale(1, 1)}.card img{width:100%;height:100%;-webkit-animation:fadeIn linear 1s;animation:fadeIn linear 1s;-webkit-transform:scale(1.2, 1.2);transform:scale(1.2, 1.2);transition:-webkit-transform linear 0.3s;transition:transform linear 0.3s;transition:transform linear 0.3s, -webkit-transform linear 0.3s}.card .data-container{position:absolute;top:0;left:0;z-index:1;width:100%;height:100%;display:flex;justify-content:center;align-items:center;font-size:1.1rem;opacity:0;transition:opacity linear 0.3s}.card .data-container li{display:flex;align-items:center;padding:10px 0}@-webkit-keyframes fadeIn{from{opacity:0}to{opacity:1}}@keyframes fadeIn{from{opacity:0}to{opacity:1}}.about p{font-size:1.1rem}.about p>a{border-bottom:2px solid #20c997}.home h1{margin-bottom:20px}

